a {
  color: #000;
  text-decoration: none;
  transition: color .2s;
  position: relative;
}

a:after {
  content: "";
  width: 100%;
  height: 2px;
  transform-origin: 100%;
  background: #4e567e;
  transition: transform .3s;
  position: absolute;
  bottom: -2px;
  left: 0;
  transform: scaleX(0);
}

a:hover:after {
  transform-origin: 0;
  transform: scaleX(1);
}

a:hover {
  color: #4e567e;
}

container {
  grid-gap: 20px;
  box-sizing: border-box;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
  display: grid;
}

.container .box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  transition: transform .3s cubic-bezier(.4, 0, .2, 1), box-shadow .3s;
  display: flex;
}

.container .box:hover {
  transform: scale(1.04)translateY(-6px);
  box-shadow: 0 8px 32px #4e567e1f;
}

.container .box:active {
  transform: scale(.98)rotate(1deg);
  box-shadow: 0 2px 8px #4e567e33;
}

img.socialIcon {
  height: 30px;
  width: 30px;
  transition: transform .2s, filter .2s;
}

img.socialIcon:hover {
  filter: brightness(1.2) drop-shadow(0 2px 8px #4e567e44);
  transform: scale(1.18)rotate(-8deg);
}

img.socialIcon:active {
  filter: brightness(1.3) drop-shadow(0 2px 12px #4e567e66);
  transform: scale(1.25)rotate(8deg);
}

button, .container .box, .socialIcon {
  transition: transform .18s cubic-bezier(.4, 0, .2, 1), box-shadow .18s, filter .18s;
}

button:active {
  transform: scale(.93)rotate(-2deg);
  box-shadow: 0 1px 4px #0003;
}

button, .container .box, .socialIcon {
  animation: bounceIn .5s cubic-bezier(.4, 0, .2, 1);
}

.cursor-trail {
  width: 22px;
  height: 22px;
  pointer-events: none;
  z-index: 3000;
  mix-blend-mode: lighten;
  background: linear-gradient(135deg, #00d4ff88 0%, #ff009688 60%, #ffd70088 100%);
  border: 2px solid #fff6;
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  transition: background .2s, transform 80ms cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%)rotate(12deg);
  box-shadow: 0 2px 12px #4e567e33;
}

@keyframes bounceIn {
  0% {
    opacity: .5;
    transform: scale(.7);
  }

  60% {
    opacity: 1;
    transform: scale(1.15);
  }

  80% {
    transform: scale(.95);
  }

  100% {
    transform: scale(1);
  }
}

.divider-svg {
  width: 100vw;
  height: 60px;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  display: block;
}

@keyframes slideInLeft {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(40px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

